import React from "react";
import "./Contact.css";

import { Phone, Email } from "@material-ui/icons";
import { Button } from "@material-ui/core";
const Contact = () => {
  return (
    <div class='contact'>
      <div className='content-wrapper'>
        <h2 className='heading'>Get In Touch</h2>
        <div className='form' style={{ marginBottom: 10, marginTop: 20 }}>
          <h2 className='contact-title'>Leave Us a Message</h2>
          <div className='form-group'>
            <div className='required'>*</div>
            <input type='text' placeholder='Your Name' />
          </div>
          <div className='form-group'>
            <div className='required'>*</div>

            <input type='email' placeholder='Email' />
          </div>
          <div className='form-group'>
            <div className='required'>*</div>

            <textarea
              className='message-input'
              placeholder='Enter Your Message'
            ></textarea>
          </div>
          <div className='form-group'>
            <Button variant='contained' color='secondary'>
              Send
            </Button>
          </div>
        </div>

        <div className='address'>
          <div class='left'>
            <li>No 2, Gyang Gwom St.</li>
            <li>Behind Gada Biu Police Station,</li>
            <li>Opp. UEC Gada Biu, Jos,</li>
            <li>Plateau State, Nigeria</li>
          </div>
          <div class='line'></div>
          <div class='right'>
            <li>
              <Phone color='secondary' /> Phone: &nbsp;
              <a href='tel:+2348104572553'>08104572553,</a>
              <a href='tel:+2348154102212'>08154102212</a>
            </li>
            <li>
              <Email color='secondary' />
              Email: &nbsp;
              <a href='mailto:ocaictcentre@gmail.com'>ocaictcentre@gmail.com</a>
            </li>
          </div>
        </div>
      </div>
      <h2>Location on Map</h2>
    </div>
  );
};

export default Contact;
