import React from "react";
import "./CourseForm.css";
import { Button } from "@material-ui/core";
import { useState } from "react";

const CourseForm = () => {
  const [course, setCourse] = useState({
    id: "ocawebdesign0010000",
    title: "Website Design (WordPress CMS)",
    requirements: [
      "Basics of computer & Graphic Design",
      "Computer with Internet Access",
    ],
    topics: [
      "Learn how to design websites of all kinds with WordPress CMS.",
      "Hosting",
    ],
    technologies: ["WordPress CMS"],
    participants: [
      "Anyone who want design professional websites of all kinds without writing a code",
      "Anyone who want to becomer web designer",
      "Web Designer who want to learn new techniques",
      "Students and Teachers",
      "School Proprietors",
      "Business Owners",
      "and More...",
    ],
    price: "35000",
    subtitle: "Learn how to design websites of all kinds with WordPress CMS.",
    level: "Beginner",
    image: "",
    rating: 5,
    instructors: [`Oluegwu Chigozie`],
    category: "web-design",
    description: `Learn how to design professional website without writing code with WordPress Content Management System.`,
    video: "",
    courseVideos: [],
  });

  return (
    <div className='courseform'>
      <h2>Add Course Details</h2>
      <div className='form'>
        <div className='form-group'>
          <input type='text' placeholder='title' />
        </div>
        <Button>Save Details</Button>
      </div>

      <div className='course-details'>
        <h2>Course OverView</h2>
        <div
          className='course-wrapper'
          style={{ maxWidth: 1024, margin: "auto" }}
        >
          <div className='course-overlay'>
            <h2 className='title'>{course.title}</h2>
            <h4>{course.subtitle}</h4>
            <h4> Level: {course.level} </h4>

            <small> Instructors: </small>
          </div>

          <h2 className='title' style={{ padding: 20 }}>
            Video Preview{" "}
          </h2>
          <video src={course.video} controls></video>

          <h2>Price: &#8358;{course.price} </h2>

          <div className='course-details-container'>
            <ul className='list-container'>
              <h3> Target Audience. </h3>
              {course.participants.map((participant, index) => (
                <li> {participant} </li>
              ))}
            </ul>

            <ul className='list-container'>
              <h3>Basic Requirments.</h3>
              {course.requirements.map((requirement, index) => (
                <li> {requirement} </li>
              ))}
            </ul>

            <ul className='list-container'>
              <h3> Technologies to be Taught</h3>
              {course.technologies.map((technology, index) => (
                <li> {technology} </li>
              ))}
            </ul>

            <ul className='list-container'>
              <h3>What you will learn</h3>
              {course.topics.map((topic, index) => (
                <li> {topic} </li>
              ))}
            </ul>
            <div className='course-description'>
              <h3>Description</h3>
              <p>
                {course.description ? course.description : "Comming Soon..."}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CourseForm;
