const defaultUser = {
  email: "",
  firstname: "",
  lastname: "",
  phone: "",
  password: "",
  password2: "",
  passport: "",
};

let data = {};
export const signupReducer = async (state = defaultUser, action) => {
  switch (action.type) {
    case "SIGN_UP":
      let user = action.payload;
      let formData = new FormData();
      formData.append("profile", user.file);
      formData.append("firstname", user.firstname);
      formData.append("lastname", user.lastname);
      formData.append("phone", user.phone);
      formData.append("password", user.password);
      formData.append("password2", user.password2);
      formData.append("email", user.email);
      try {
        let res = await fetch("http://localhost:3600/mobile-api", {
          method: "post",
          body: formData,
        });
        let resData = await res.json();
        return resData;
      } catch (error) {
        console.log(error);
      }
      break;
    default:
      return state;
  }
};

const defaultLogin = { email: "", password: "" };
export const loginReducer = (state = defaultLogin, action) => {
  switch (action.type) {
    case "LOG_IN":
      return { ...state, ...action.payload };
      break;
    default:
      return state;
  }
};
