import { Button } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";
import Technologies from "../Tecnologies/Technologies";
import "./About.css";
const About = () => {
  return (
    <div className='about'>
      <div className='container'>
        <div className='text-bg'>
          <div className='about-overlay'>
            <div className='text-wrapper'>
              <h2 className='heading'>About Us</h2>

              {/* <h2 className='title'>We are here for all your web needs</h2> */}

              <p className='desc'>
                We build and deploy Mobile/Desktop Application and eLearning
                Contents
              </p>

              <h2 className='heading'>Web Development Training</h2>
              <p className='desc'>
                Become a Front-End or FullStack Web Developer by learning from
                experts. Learn Html5, Css3, JavaScript+, BootStrap, jQuery,
                ReactJS, Angular, Ionic Framework, PHP, NodeJS, MongoDB, MYSSQL
                and More..
              </p>
            </div>
          </div>
        </div>

        <div className='content-wrapper'>
          <h2 className='title' style={{ color: "black" }}>
            Technologies We Speciallize On
          </h2>
          <Technologies />
          <Button variant='contained' fullWidth={true} color='secondary'>
            Start Learning
          </Button>
        </div>
      </div>
    </div>
  );
};

export default About;
