import React, { useState } from "react";
import "./Technologies.css";

import html5Icon from "../../assets/icons/html5.svg";
import bootstrapIcon from "../../assets/icons/bootstrap.svg";
import css3Icon from "../../assets/icons/css3.svg";
import javaScriptIcon from "../../assets/icons/javascript.svg";
import jQueryIcon from "../../assets/icons/jquery.svg";
import reactIcon from "../../assets/icons/react.svg";
import angularIcon from "../../assets/icons/angular.svg";
import nodejsIcon from "../../assets/icons/nodejs.svg";
import phpIcon from "../../assets/icons/php.svg";
import pythonIcon from "../../assets/icons/python.svg";
import cordovaIcon from "../../assets/icons/cordova.png";
import mysqlIcon from "../../assets/icons/mysql.svg";
import mongodbIcon from "../../assets/icons/mongodb.svg";
import sqlIcon from "../../assets/icons/sql.svg";
import moodleIcon from "../../assets/icons/moodle.svg";
import captivateIcon from "../../assets/icons/captivate.svg";

const Technologies = () => {
  const [frontEndVisible, setFrontEndVisible] = useState(true);
  const [backEndVisible, setBackEndVisible] = useState(false);
  const [mobileVisible, setmobileVisible] = useState(false);
  const [databaseVisible, setDatabaseVisible] = useState(false);
  const [elearningVisible, setElearningVisible] = useState(false);

  const showFrontEnd = () => {
    setFrontEndVisible(true);
    setBackEndVisible(false);
    setmobileVisible(false);
    setDatabaseVisible(false);
    setElearningVisible(false);
  };

  const showBackEnd = () => {
    setBackEndVisible(true);
    setFrontEndVisible(false);
    setmobileVisible(false);
    setDatabaseVisible(false);
    setElearningVisible(false);
  };
  const showMobile = () => {
    setBackEndVisible(false);
    setFrontEndVisible(false);
    setmobileVisible(true);
    setDatabaseVisible(false);
    setElearningVisible(false);
  };
  const showDatabases = () => {
    setDatabaseVisible(true);
    setBackEndVisible(false);
    setFrontEndVisible(false);
    setmobileVisible(false);
    setElearningVisible(false);
  };
  const showElearning = () => {
    setElearningVisible(true);
    setDatabaseVisible(false);
    setBackEndVisible(false);
    setFrontEndVisible(false);
    setmobileVisible(false);
  };

  return (
    <div className='technology-container'>
      <div className='technology-btn-wrapper'>
        <div
          className={
            frontEndVisible
              ? "technology-btn front-btn active"
              : "technology-btn front-btn"
          }
          onClick={showFrontEnd}
        >
          Front-End
        </div>
        <div
          className={
            backEndVisible
              ? "technology-btn back-btn active "
              : "technology-btn back-btn "
          }
          onClick={showBackEnd}
        >
          Back-End
        </div>
        <div
          className={
            mobileVisible
              ? "technology-btn mobile-desktop-btn active"
              : "technology-btn mobile-desktop-btn"
          }
          onClick={showMobile}
        >
          Mobile/Desktop
        </div>
        <div
          className={
            databaseVisible
              ? "technology-btn databases-btn active"
              : "technology-btn databases-btn "
          }
          onClick={showDatabases}
        >
          Databases
        </div>
        <div
          className={
            elearningVisible
              ? "technology-btn elearnings-btn active"
              : "technology-btn elearnings-btn"
          }
          onClick={showElearning}
        >
          Elearning
        </div>
      </div>

      <div className='technology-group'>
        {frontEndVisible && (
          <div className='technology-wrapper front-end'>
            <div className='technology'>
              <img alt='' src={html5Icon} />
              <h4>HTML5</h4>
            </div>

            <div className='technology'>
              <img alt='' src={css3Icon} />
              <h4>CSS3</h4>
            </div>
            <div className='technology'>
              <img alt='' src={bootstrapIcon} />
              <h4>Bootstrap</h4>
            </div>
            <div className='technology'>
              <img alt='' src={javaScriptIcon} />
              <h4>JavaScript</h4>
            </div>
            <div className='technology'>
              <img alt='' src={jQueryIcon} />
              <h4>JQUERY</h4>
            </div>

            <div className='technology'>
              <img alt='' src={reactIcon} />
              <h4>ReactJS</h4>
            </div>

            <div className='technology'>
              <img alt='' src={angularIcon} />
              <h4>Angular</h4>
            </div>
          </div>
        )}

        {backEndVisible && (
          <div className='technology-wrapper back-end'>
            <div className='technology'>
              <img alt='' src={nodejsIcon} />
              <h4>Node JS</h4>
            </div>
            <div className='technology'>
              <img alt='' src={phpIcon} />
              <h4>PHP</h4>
            </div>
            <div className='technology'>
              <img alt='' src={pythonIcon} />
              <h4>Python</h4>
            </div>
          </div>
        )}

        {mobileVisible && (
          <div className='technology-wrapper mobile-desktop'>
            <div className='technology'>
              <img alt='' src={reactIcon} />
              <h4>React Native</h4>
            </div>
            <div className='technology'>
              <img alt='' src={cordovaIcon} />
              <h4>Capacitor/Cordova</h4>
            </div>
            <div className='technology'>
              <img alt='' src={reactIcon} />
              <h4>Electron JS</h4>
            </div>
            <div className='technology'>
              <img alt='' src={pythonIcon} />
              <h4>Python</h4>
            </div>
          </div>
        )}
        {databaseVisible && (
          <div className='technology-wrapper databases'>
            <div className='technology'>
              <img alt='' src={mysqlIcon} />
              <h4>MYSQL</h4>
            </div>
            <div className='technology'>
              <img alt='' src={mongodbIcon} />
              <h4>MongoDB</h4>
            </div>
            <div className='technology'>
              <img alt='' src={sqlIcon} />
              <h4>SQLITE</h4>
            </div>
          </div>
        )}

        {elearningVisible && (
          <div className='technology-wrapper elearning '>
            <div className='technology'>
              <img alt='' src={moodleIcon} />
              <h4>Moodle LMS</h4>
            </div>
            <div className='technology'>
              <img alt='' src={captivateIcon} />
              <h4>Captivate</h4>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Technologies;
