import { Button } from "@material-ui/core";
import React from "react";
import { useRef } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { signup } from "../../../Actions/actions";
import { getFormDetails, clearInputs } from "../../../Functions/functions";
import "./SignUpForm.css";

import avatar from "../../../assets/images/user.png";
import { useEffect } from "react";
const SignUpForm = () => {
  const dispatch = useDispatch();
  const [error, setError] = useState(null);
  const [passport, setPassport] = useState(null);
  const [success, setSuccess] = useState(false);
  const formRef = useRef(null);

  useEffect(() => {}, [success]);

  const handleSubmit = () => {
    let passportLabel = document.querySelector(".passport-label");

    let user = getFormDetails(formRef.current);
    if (
      user.email &&
      user.firstname &&
      user.lastname &&
      user.password &&
      user.password == user.password2 &&
      passport
    ) {
      user.file = passport;
      dispatch(signup(user));
      setError(null);

      clearInputs(formRef.current);
      passportLabel.style.background = `url(${avatar}) no-repeat center center`;
      passportLabel.style.backgroundSize = `cover`;
      setPassport(null);
    } else {
      setError("Fill all fields correctly and try again");
    }
  };

  const handleChange = (file) => {
    let passportLabel = document.querySelector(".passport-label");
    let fileReader = new FileReader();
    fileReader.onloadend = function () {
      passportLabel.style.background = `url(${this.result}) no-repeat center center`;
      passportLabel.style.backgroundSize = `cover`;
      setPassport(file);
    };
    fileReader.readAsDataURL(file);
  };

  return (
    <div className='signup-form-container'>
      <h2 className='title'>
        Let<sup>●</sup>s Develop Website Together!
      </h2>
      <div className='form signup-form' ref={formRef}>
        {error && <small className='error'>{error}</small>}

        <div className='passport-container'>
          <label className='passport-label' htmlFor='passport'>
            <input
              type='file'
              name='profile'
              id='passport'
              accept='image/*'
              onChange={(e) => handleChange(e.target.files[0])}
            />
          </label>
        </div>
        <div className='form-group'>
          <input
            type='email'
            name='email'
            placeholder='Email'
            autoComplete='off'
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            name='firstname'
            placeholder='First Name'
            autoComplete='off'
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            name='lastname'
            placeholder='Last Name'
            autoComplete='off'
          />
        </div>
        <div className='form-group'>
          <input
            type='tel'
            name='phone'
            placeholder='Phone: e.g 080709038899'
            autoComplete='off'
          />
        </div>
        <div className='form-group'>
          <input type='password' name='password' placeholder='Password' />
        </div>
        <div className='form-group'>
          <input
            type='password'
            name='password2'
            placeholder='Confirm Password'
          />
        </div>
        <div className='btn-group'>
          <Button
            variant='contained'
            color='primary'
            onClick={() => handleSubmit()}
          >
            Sign Up
          </Button>
        </div>
      </div>
    </div>
  );
};

export default SignUpForm;

const styles = {
  error: {
    color: "#fff",
    backgroundColor: "salmon",
    width: "100%",
    padding: "5px 10px",
    marginBottom: "50px",
  },
};
