import styled from "styled-components";

const Container = styled.div`
  width: 600px;
  height: 600px;
  padding: 30px;
  background-color: darkcyan;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  color: ${({ color }) => color};
  margin: auto;
  padding: 30px;
`;

export default Container;
