function toHex(value) {
  const hex = "0123465789ABCDEF";
  let result = hex.charAt(Math.floor(value / 16));
  result += hex.charAt(value % 16);
  return result;
}
export function setTitle(element, text, colorBegin, colorEnd) {
  //   const colorBegin = "#DC143C";
  //   const colorEnd = "#3CB371";
  // const text = "Oca WebTech";
  let r = parseInt(colorBegin.substring(1, 3), 16);
  let g = parseInt(colorBegin.substring(3, 5), 16);
  let b = parseInt(colorBegin.substring(5, 7), 16);
  const rr = parseInt(colorEnd.substring(1, 3), 16);
  const gg = parseInt(colorEnd.substring(3, 5), 16);
  const bb = parseInt(colorEnd.substring(5, 7), 16);
  const r_step = (rr - r) / text.length;
  const g_step = (gg - g) / text.length;
  const b_step = (bb - b) / text.length;
  let html = "";
  for (let x = 0; x <= text.length; x++) {
    html =
      html +
      '<span style="color:#' +
      toHex(r) +
      toHex(g) +
      toHex(b) +
      '";">' +
      text.charAt(x) +
      "</span>";
    r += r_step;
    g += g_step;
    b += b_step;
  }
  element.innerHTML = html;
}

export const getFormDetails = (form) => {
  let user = {};
  let formInputs = form.querySelectorAll("input");
  formInputs.forEach((input) => {
    user[input.name] = input.value;
  });

  return user;
};
export const clearInputs = (form) => {
  let formInputs = form.querySelectorAll("input");
  formInputs.forEach((input) => {
    input.value = "";
  });
};

export const getRequestFormInputValues = (form) => {
  let data = {};
  const formInputs = form.querySelectorAll("input");
  const formTextAreas = form.querySelectorAll("textarea");
  const formSelects = form.querySelectorAll("select");

  const textAreas = {};
  const selectFields = {};
  formInputs.forEach((input) => {
    data[input.name] = input.value;
  });

  formTextAreas.forEach((input) => {
    textAreas[input.name] = input.value;
  });
  formSelects.forEach((input) => {
    selectFields[input.name] = input.value;
  });

  data = { ...data, ...textAreas, ...selectFields };
  return data;
};
