import { Button } from "@material-ui/core";
import React from "react";
import { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Router, useto } from "react-router-dom";
import { increment } from "../../Actions/actions";
import "./Header.css";
import Logo from "../../assets/images/logo.png";
import { setTitle } from "../../Functions/functions";

export const Header = ({ user }) => {
  const titleEle = useRef(null);
  const subTitleEle = useRef(null);
  const dispatch = useDispatch();
  const { signupReducer } = useSelector((state) => state);

  const [data, setData] = useState({});

  useEffect(async () => {
    setData(await signupReducer);

    setTitle(titleEle.current, "OcaWebTech", "#DC143C", "#3CB371");
  }, []);

  return (
    <header className='header'>
      <Link to={"/"} className='logo' style={{ fontSize: 28 }}>
        {/* <img width='40' height='40' src={Logo} alt='Oca' /> */}
        <h1 className='title' ref={titleEle} style={{ fontSize: 24 }}>
          Oca WebTech
        </h1>
      </Link>

      <input type='checkbox' className='menu-btn' id='menu-btn' />
      <label htmlFor='menu-btn' className='menu-icon'>
        {" "}
        <span className='nav-icon'></span>
      </label>

      <ul className='menu'>
        <li>
          <Link className='link' to='/about'>
            {" "}
            About
          </Link>
        </li>

        <li>
          <Link className='link' to='/services'>
            Services
          </Link>
        </li>

        <li>
          <Link className='link' to='/contact'>
            Contact
          </Link>
        </li>
        <li>
          <Link className='link' to='/courses'>
            Training
          </Link>
        </li>
        <li>
          <Link className='link' to='/request'>
            Hire Developers
          </Link>
        </li>
        <li>
          <Link className='link' to='/projects'>
            Projects
          </Link>
        </li>
      </ul>
    </header>
  );
};
