import { Button } from "@material-ui/core";
import React from "react";
import { useState } from "react";
import { useParams, Navigate } from "react-router";
import { Link } from "react-router-dom";
import "./Course.css";
import certificateImage from "../../../assets/images/certificate.jpg";
import { courseData } from "../coursesData";
import { useEffect } from "react";
import CourseBackground from "../../styles/CourseImage.styled";

const Course = () => {
  let { courseId } = useParams();

  useEffect(() => {
    getCourse(courseId);
  }, []);

  const [course, setCourse] = useState(null);

  const getCourse = (id) => {
    setCourse(courseData.find((course) => course.id == id));
  };

  const addRating = (n) => {
    let star = [];
    for (let i = 0; i < n; i++) {
      star.push(<span>&#x2605;</span>);
    }
    return star;
  };

  const truncateText = (text) => {
    if (text.length > 100) {
      return text.substring(0, 100) + "...";
    } else return text;
  };
  return (
    <div className='course-container'>
      {course && (
        <div className='course-wrapper'>
          <CourseBackground image={course?.image}>
            <div className='course-overlay'>
              <button className='c-btn go-back-btn'>Back</button>

              <h2 className='title'>{course.title}</h2>
              <h4>{course.subtitle}</h4>
              <h4> Level: {course.level} </h4>

              <small> Instructor: {course.instructor} </small>
              <h5 className='rating'>
                {" "}
                Rating:{" "}
                <span>
                  {course.rating ? addRating(course.rating) : addRating(1)}
                </span>{" "}
              </h5>
            </div>
          </CourseBackground>
          <h2 className='title' style={{ padding: 20 }}>
            Video Preview{" "}
          </h2>
          <video src={course.video} controls></video>

          <h2>Price: &#8358;{course.price} </h2>
          <button className='c-btn buy-btn'> Apply Now </button>

          <div className='course-details-container'>
            <ul className='list-container'>
              <h3> Target Audience. </h3>
              {course.participants.map((participant, index) => (
                <li> {participant} </li>
              ))}
            </ul>

            <ul className='list-container'>
              <h3>Basic Requirments.</h3>
              {course.requirements.map((requirement, index) => (
                <li> {requirement} </li>
              ))}
            </ul>

            <ul className='list-container'>
              <h3> Technologies to be Taught</h3>
              {course.technologies.map((technology, index) => (
                <li> {technology} </li>
              ))}
            </ul>

            <ul className='list-container'>
              <h3>What you will learn</h3>
              {course.topics.map((topic, index) => (
                <li> {topic} </li>
              ))}
            </ul>
            <div className='course-description'>
              <h3>Description</h3>
              <p>
                {course.description
                  ? truncateText(course.description)
                  : "Comming Soon..."}
              </p>
            </div>
          </div>
          <button className='c-btn buy-btn'> Apply Now </button>
        </div>
      )}
    </div>
  );
};

export default Course;
