import { Button } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";
import "./Courses.css";
import { courseData } from "./coursesData";

// import WebDevImage from "../../assets/images/certificate.jpg";
import { useState } from "react";
const Courses = () => {
  const [courses, setCourses] = useState(courseData.slice(0, 3));
  const [showLess, setShowLess] = useState(true);

  const showLessOrMore = () => {
    setShowLess(!showLess);
    if (showLess) {
      setCourses(courseData);
    } else {
      setCourses(courseData.slice(0, 3));
    }
  };

  const addRating = (n) => {
    let star = [];
    for (let i = 0; i < n; i++) {
      star.push(<span>&#x2605;</span>);
    }
    return star;
  };

  return (
    <div className='courses'>
      <div className='courses-bg'>
        <div className='courses-overlay'>
          <h2 className='heading' style={{ color: "#fff" }}>
            Our Training
          </h2>
          <h2 className='title' style={{ color: "#fff" }}>
            We are here for all your Web Training needs
          </h2>
          <div className='card-wrapper'>
            {courses.map((course) => (
              <div className='card' key={course.id}>
                <div className='card-image'>
                  <img src={course.image} alt='' />
                </div>
                <div className='card-header'>
                  <h2 className='title'>
                    {course.title}
                    <br /> <span className='subtitle'>{course?.subtitle}</span>
                  </h2>
                  <h3 className='level'>{course?.level}</h3>
                </div>

                <div className='card-footer'>
                  <h4 className='instructor'>
                    Instructor: <span>{course?.instructor}</span>
                  </h4>
                  <small>
                    Rating:
                    {course.rating ? (
                      <span className='rating-container'>
                        {addRating(course.rating)}
                      </span>
                    ) : (
                      "No Rating"
                    )}
                  </small>
                  <Link className='btn' to={`/courses/${course.id}`}>
                    <Button variant='contained' color='secondary' size='small'>
                      Learn More
                    </Button>
                  </Link>
                </div>
              </div>
            ))}
          </div>
          <div className='btn-wrapper'>
            <Button
              variant='contained'
              color='primary'
              fullWidth={true}
              onClick={() => showLessOrMore()}
            >
              {!showLess ? "Show Less" : "Show More"}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Courses;
