import { Button } from "@material-ui/core";
import React from "react";
import { useRef } from "react";
import { getRequestFormInputValues } from "../../../Functions/functions";
import "./MyRequestForm.css";
const RequestForm = () => {
  // State Management
  const requestFormRef = useRef(null);
  // References
  const sendRequest = async () => {
    const data = getRequestFormInputValues(requestFormRef.current);
    if (!data.email) return alert("Provide Email");
    let emailRule = /^([a-z\d-]+)@([a-z\d-]+)\.([a-z]{2,8})(\.[a-z]{2,8})?$/;
    let validEmail = emailRule.test(data.email);
    if (!validEmail) return alert("Enter a valid Email");

    const res = await fetch("http://localhost:3600/request", {
      method: "post",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });

    const resData = await res.json();
  };

  return (
    <div className='request-form-container'>
      <div className='form-wrapper'>
        <div className='text-wrapper'>
          <h2 className='title'>Website Development Request Form</h2>
        </div>

        <div
          className='form request-form'
          style={{ maxWidth: "100%" }}
          ref={requestFormRef}
        >
          {/* Names */}
          <div className='input-group'>
            <div className='form-group'>
              <label htmlFor='firstname'>First Name</label>
              <input
                id='firstname'
                type='text'
                name='firstname'
                placeholder='First Name'
                autoComplete='off'
              />
            </div>
            <div className='form-group'>
              <label htmlFor='lastname'>Last Name</label>

              <input
                id='lastname'
                type='text'
                name='lastname'
                placeholder='Last Name'
                autoComplete='off'
              />
            </div>
            <div className='form-group'>
              <label htmlFor='middle-name'>Other Names</label>

              <input
                id='middle-name'
                type='text'
                name='middleName'
                placeholder='Other Name'
                autoComplete='off'
              />
            </div>
          </div>
          {/* Contacts */}
          <div className='input-group'>
            <div className='form-group' style={{ flex: 2 }}>
              <label htmlFor='email'>Email</label>
              <input
                id='email'
                type='email'
                name='email'
                placeholder='Email'
                autoComplete='off'
              />
            </div>
            <div className='form-group'>
              <label htmlFor='phone'>Office Tel</label>
              <input
                id='phone'
                type='tel'
                name='phone'
                placeholder='Phone: e.g 080709038899'
                autoComplete='off'
              />
            </div>
            <div className='form-group'>
              <label htmlFor='mobile'>Mobile</label>
              <input
                id='mobile'
                type='tel'
                name='mobile'
                placeholder='Phone: e.g 080709038899'
                autoComplete='off'
              />
            </div>
          </div>

          <div className='form-group'>
            <label htmlFor='address'>Contact Address</label>
            <textarea
              id='address'
              type='text'
              name='address'
              placeholder='No 19 Rwan Pam St. Jos Plateau State'
              autoComplete='off'
            ></textarea>
          </div>

          <div className='input-group'>
            <div className='form-group'>
              <label htmlFor='business-name'>Business Name</label>
              <input
                id='business-name'
                type='text'
                name='businessName'
                placeholder='OCA WEBTECH'
                autoComplete='off'
              />
            </div>
            <div className='form-group'>
              <label htmlFor='business-location'>Business Location</label>
              <input
                id='business-location'
                type='text'
                name='businessLocation'
                placeholder='Jos'
                autoComplete='off'
              />
            </div>
          </div>

          <div className='check-wrapper'>
            <div>
              {" "}
              <label>What is your time frame for this project?</label>
            </div>
            <div className='check-group'>
              <label htmlFor='web'>Web Design</label>
              <input
                type='checkbox'
                name='service'
                id='web'
                value='Web Design'
                onChange={(e) => console.log(e.target.value)}
              />
            </div>
            <div className='check-group'>
              <label htmlFor='mobile'>Mobile</label>
              <input
                type='checkbox'
                name='service'
                id='mobile'
                value='Mobile'
                onChange={(e) => console.log(e.target.value)}
              />
            </div>
            <div className='check-group'>
              <label htmlFor='mobile'>Training</label>
              <input
                type='checkbox'
                name='service'
                id='mobile'
                value='Mobile'
                onChange={(e) => console.log(e.target.value)}
              />
            </div>
            <div className='check-group'>
              <label htmlFor='consulting'>Consulting</label>
              <input
                type='checkbox'
                name='service'
                id='consulting'
                value='Mobile'
                onChange={(e) => console.log(e.target.value)}
              />
            </div>
          </div>

          <div className='form-group'>
            <label htmlFor='business-location'>
              Please type in other information you have:
            </label>
            <textarea name='otherInfo'></textarea>
          </div>

          <div className='btn-group'>
            <Button
              variant='contained'
              color='primary'
              b
              onClick={() => sendRequest()}
            >
              Send Request
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RequestForm;
